
.box {

}

.img {
  width: 45px;
  object-fit: cover;
  position: relative;

  &_left {
    transform: scale(1, -1);
  }

  &_right {
    transform: scale(1, 1);
  }
}


@media only screen and (max-width: 950px) {
  .img {
    width: 40px;
  }
}

@media only screen and (max-width: 620px) {
  .img {
    width: 35px;
  }
}

@media only screen and (max-width: 400px) {
  .img {
    width: 25px;
  }
}