.box {
  width: max-content;
  margin: 0 auto;
  margin-top: 20px;
}

.title {
  font-weight: 800;
  font-size: 3.8rem;
  line-height: 100%;
  color: white;

  letter-spacing: 1px;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.13);
  text-transform: uppercase;
  margin: 0;
}

.coin {
  margin-bottom: -10px;
  height: 3.8rem;
}

@media only screen and (max-width: 520px) {
  .title {
    font-size: 3rem;
  }
  .coin {
    margin-bottom: -5px;
    height: 3rem;
  }
}

@media only screen and (max-width: 410px) {
  .title {
    font-size: 2rem;
  }
  .coin {
    height: 2rem;
  }
}
