
.box {
  max-width: 110px;
  width: 100%;
}

.img {
  width: 100%;
  height: auto;

  &_left {
    transform: scale(-1, 1);
    margin-left: 30px;
  }

  &_right {
    transform: scale(1, 1);
    margin-left: -30px;
  }
}

@media only screen and (max-width: 950px) {
  .box {
    max-width: 90px;
  }
}

@media only screen and (max-width: 620px) {
  .box {
    max-width: 50px;
  }

  .img {
    position: relative;
    top: 25px;
  }
}

@media only screen and (max-width: 400px) {
  .box {
    max-width: 30px;
  }
}