.title,
.subTitle {
  text-transform: uppercase;
  width: max-content;
  margin: 0;
}

.title {
  font-weight: normal;
  font-size: 4rem;
  line-height: 100%;
  color: #00ffc6;

  letter-spacing: -2px;
}

.subTitle {
  font-weight: 800;
  font-size: 2rem;
  line-height: 100%;
  color: #ff438c;

  letter-spacing: 1px;
}

.box {
  position: absolute;
  top: -45px;
  left: 50%;
  margin-left: -95px;
}

@media only screen and (max-width: 900px) {
  .box {
    top: -60px;
  }
}
