.box {
  width: min-content;
  margin: 0 auto;
  margin-top: -100px;
  display: flex;
  justify-content: center;
  box-sizing: border-box;

  border-radius: 20px;
  overflow: hidden;
  padding: 20px;
  background: #150a30;
}

.wrapper {
  width: 800px;
  height: 400px;
  position: relative;
}

.img {
  position: absolute;
  z-index: 1;
  opacity: 0.4;
  bottom: 0;
  width: 100%;
  transform: scale(1, -1) perspective(150px);
}

@media only screen and (max-width: 900px) {
  .box,
  .wrapper {
    width: 100%;
  }
}

@media only screen and (max-width: 450px) {
  .box {
    border-radius: 0;
  }

  .wrapper {
    height: 200px;
  }
}

@media only screen and (max-width: 400px) {
  .box {
    padding: 10px;
  }
}
