.background {
  height: max-content;
  width: 100%;
  padding-bottom: 50px;
  background: transparent;

  font-family: "Gilroy", sans-serif;
  font-weight: normal;
  font-size: 10px;
  color: white;
  line-height: 100%;
}

.gradient {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  background: linear-gradient(180deg, #290860 0%, #050539 100%);
}

.roof {
  position: relative;
  z-index: 3;
  margin-bottom: 50px;
}

.basement {
  position: relative;
  z-index: 3;
}

.terms {
  position: fixed;
  z-index: 5;
  bottom: 20px;
  right: 5%;
}

@media only screen and (max-width: 800px) {
  .roof {
    margin-bottom: 100px;
  }
}

@media only screen and (max-width: 480px) {
  .roof {
    margin-bottom: 150px;
  }

  .terms {
    position: static;
  }
}

@media only screen and (max-width: 800px) {
  .terms {
    position: static;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    margin-top: 50px;
  }
}
