.box {
  width: max-content;
  margin: 50px 0 0;
}

.numWrapper {
  margin: 0 auto;
  height: 35px;
  width: 35px;
  border-radius: 50%;
  background: $buttonTextColor;
  display: flex;
  justify-content: center;
  align-items: center;
}

.num {
  font-weight: bold;
  font-size: 1.8rem;
  line-height: 100%;
  color: #00ffc8;

  margin: 0;
  letter-spacing: 1px;
}

.textWrapper {
  max-width: 190px;
}

.text {
  font-weight: bold;
  font-size: 1.8rem;
  line-height: 100%;
  color: #00ffc8;

  text-align: center;
  width: 100%;
  margin: 0 auto;
  margin-top: 20px;
}
