.box {
  width: max-content;
  margin: 0 auto;
  margin-top: 20px;
}

.text {
  font-weight: normal;
  font-size: 2.1rem;
  line-height: 100%;
  color: white;

  filter: drop-shadow(0px 1px 1px #00000021);
  letter-spacing: 2px;
  text-transform: uppercase;
}

@media only screen and (max-width: 520px) {
  .text {
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 330px) {
  .text {
    font-size: 1.1rem;
  }
}
