.box {
  width: 100%;
  margin: 20px 0;
}

.button {
  width: 100%;
  border: none;
  background: #00ffc8;
  box-shadow: 0 0 12px 0 #34ecff66;
  border-radius: 15px;

  font-weight: 800;
  font-size: 3rem;
  line-height: 100%;
  color: #330e8d;

  text-transform: uppercase;
  padding: 15px 0;

  outline: none;
  cursor: pointer;
  transition: transform 0.5s;
}

.button:hover {
  transform: scale(1.05);
}

@media only screen and (max-width: 520px) {
  .button {
    font-size: 2rem;
  }
}
