.box {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.leftArrow,
.rightArrow {
  position: absolute;
  top: 50%;
  margin: -20px;
  margin-top: -30px;
}

.leftArrow {
  left: -70px;
}

.rightArrow {
  right: -70px;
  transform: scale(-1, 1);
}


@media only screen and (max-width: 666px) {
  .box {
    display: none;
  }
}
