.box {
  display: none;

  font-weight: 800;
  font-size: 3rem;
  line-height: 100%;
  color: #330e8d;

  text-transform: uppercase;
  border-radius: 15px;
  background: #00ffc8;
  padding: 15px 25px;
  margin-bottom: 25px;

  border: none;
  outline: none;
  cursor: pointer;
  box-shadow: 0 0 12px 0 #34ecff66;

  transition: transform 0.5s;
}
.box:hover {
  transform: scale(1.05);
}

@media only screen and (max-width: 800px) {
  .box {
    display: block;
  }
}
