.box {
  position: relative;
  top: -70px;

  width: min-content;
  margin: 0 auto;
  padding: 20px 40px;
  box-sizing: border-box;
  border-radius: 38px;
  background-color: #5e38ba;
}

@media only screen and (max-width: 900px) {
  .box {
    padding: 10px 20px;
  }
}

@media only screen and (max-width: 700px) {
  .box {
    top: -10px;
  }
}
