.box {
  max-width: 800px;
  width: 100%;
}

.upText {
  display: flex;
  justify-content: center;
  margin: 75px 20px 0;
}

.downText {
  display: flex;
  justify-content: center;
  margin: 20px 0 0;
}

.leftText {
  transform: rotate(-3deg);
  margin-right: 10px;
}

.rightText {
  transform: rotate(3deg);
  margin-left: 10px;
}

.centerText {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.multiplierWrapper,
.greetingWrapper {
  width: 100%;
}

.multiplierWrapper {
  margin-top: -50px;
}

.multiplierImg,
.greetingImg {
  width: 100%;
  margin: -80px 0;
}

@media only screen and (max-width: 900px) {
  .multiplierImg {
    margin-top: -50px;
  }
}

@media only screen and (max-width: 400px) {
  .upText {
    margin-top: 45px;
  }
}
