.box {
  max-width: 600px;
  width: 100%;
  margin: 0 -60px;
}

.upText {
  display: flex;
  justify-content: center;
  margin: 0;
}

.leftText {
  transform: rotate(-3deg);
  margin-right: 10px;
}

.centerText {
  transform: rotate(0deg);
  margin: 0 10px;
  margin-top: -4px;
}

.rightText {
  transform: rotate(3deg);
  margin-left: 10px;
}

.adviceWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}

.adviceImg {
  width: 100%;
  margin: -80px 0;
}

@media only screen and (max-width: 450px) {
  .adviceImg {
    width: 80%;
  }
}
