.box {
  height: 150px;
  max-width: 1500px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: -70px;
}

.img {
  margin-top: 10px;
  width: 100%;
  object-fit: cover;
}

@media only screen and (max-width: 900px) {
  .box {
    margin-bottom: -100px;
  } 
}

@media only screen and (max-width: 500px) {
  .box {
    margin-bottom: -120px;
  } 
}